#lower-img{
    width: 100%;
    overflow: hidden;
    padding: 0px; 
    margin: 0px;
    background-size: cover;
    position: relative;
}

#lower-img-div{
  position: absolute;
  top: 80%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: transparent;
  font-size: 60px;
  color: #fff;
  text-align: center;

  @media screen and (max-width: 400px) {
     font-size: 10px;
    
}

@media screen and (max-width: 500px) {
   
     font-size: 20px;
    
}
}

#lower-img-small{
    position: absolute;
    top: 90%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: transparent;
    font-size: 20px;
    color: #fff;

    @media screen and (max-width: 500px) {
        div {
         font-size: 10px;
        }
}
}

